.friends-list-container {
  font-family: sans-serif;
  width: 300px;
  margin: 0 auto;
  padding: 10px;
  border: 1px solid #ccc;
}
.title {
  background: #f8f8f8;
  padding: 15px 10px;
  font-weight: 600;
}
input {
  width: 100%;
  padding: 0;
  line-height: 40px;
  border: none;
  border-bottom: 1px solid #ccc;
}
input:focus {
  outline: none;
}
.search-friend-container {
  margin: 10px 0px;
  position: relative;
}
.material-icons.search {
  position: absolute;
  cursor: pointer;
  top: 10px;
  right: 10px;
}
ul.friend-list {
  padding-left: 0px;
}
li.page,
li.friend {
  list-style: none;
  padding: 5px 0px;
}
li.friend {
  border-bottom: 1px solid #ccc;
}

a.friend-name {
  text-decoration: none;
  color: #000000;
  font-weight: 600;
  line-height: 25px;
}
.sub-text {
  line-height: 25px;
  font-size: 14px;
  color: #5d5d5d;
}
.page-numbers {
  display: flex;
}
.delete,
.fav,
li.page {
  width: 30px;
  margin: 5px;
  line-height: 30px;
  border: 1px solid #ccc;
  text-align: center;
}
.sort-by-fav {
  display: flex;
  flex-flow: column;
}
.material-icons.sort {
  text-align: right;
  cursor: pointer;
}
.material-icons.fav,
.material-icons.delete {
  cursor: pointer;
  float: right;
}
.error-msg-container {
  font-size: 12px;
  color: red;
}
@media (max-width: 767px) {
  .friends-list-container {
    width: 80%;
    margin: 0 auto;
  }
}
